const ADD_GUN = "";
const REMOVE_GUN = "";

export function counter(state = 10, aciton) {
  console.log(state)
  switch (aciton.type) {
    case ADD_GUN:
      return state + 1;
    case REMOVE_GUN:
      return state - 1;
    default:
      return state;
  }
}

export function addGun() {
  return { type: ADD_GUN };
}

export function removeGun() {
  return { type: REMOVE_GUN };
}

export function addGunTwice() {
  return  { type: ADD_GUN }
}

export function addGunAsync() {
  return dispatch => {
    setTimeout(() => {
      dispatch(addGun());
    }, 2000);
  };
}




