import React from "react";
import { Link, Route, Redirect} from "react-router-dom";
import App from './App';
import { connect } from 'react-redux';
import { logout } from './auth.redux'

// @connect(state=>(state=>state.auth, {logout})

const Find = () => {
  return <h2></h2>;
};

const Member = () => {
  return <h2></h2>;
};

class Test extends React.Component {


  render() {
    console.log(this.props);
    return <h2>== {this.props.match.params.location}</h2>;
  }
}




class Dashbord extends React.Component {

  render() {
    console.log('dashbord',this.props)
    const match = this.props.match;
    const redirectTologin = <Redirect to="/login"></Redirect>
    const app = (
    <div>
      <h2>Dashbord Page</h2>
      { this.props.isAuth ?  <button onClick={this.props.logout}></button>: null }
     
      <ul>
        <li>
          <Link to={`${match.url}/`}></Link>
        </li>
        <li>
          <Link to="/dashbord/find?a=5"></Link>
        </li>
        <li>
          <Link to="/dashbord/member"></Link>
        </li>
      </ul>

      <Route path="/dashbord/" exact component={App} />
      <Route path="/dashbord/find" component={Find} />
      <Route path="/dashbord/member" component={Member} />
      <Route path="/dashbord/:location" component={Test} />
    </div>)
    return (
        this.props.isAuth ? app : redirectTologin
    )
  }
}

// 
const myStatetoProps = (state) => {
  return state.auth
}
const acitonCreators = {logout};
// 
Dashbord = connect(myStatetoProps, acitonCreators)(Dashbord);

export default Dashbord;



